import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { BigCustomApplyComponent } from './bigCustomApply/bigCustomApply.component';//
import { BigCustomHistoryQueryComponent } from './bigCustomHistoryQuery/bigCustomHistoryQuery.component';//
import { BigCustomApplyQueryComponent } from './bigCustomApplyQuery/bigCustomApplyQuery.component';
import { BigCustomDetailComponent } from './bigCustomDetail/bigCustomDetail.component';//

export const routes: Routes = [
    {
        path: '',
        children: [
            { path: '', redirectTo: 'bigCustomApply', pathMatch: 'full' },
            {
                path: 'bigCustomApply',
                component: BigCustomApplyComponent
            },
            {
                path: 'bigCustomHistoryQuery',
                component: BigCustomHistoryQueryComponent,
            },
            {
                path: 'bigCustomApplyQuery',
                component: BigCustomApplyQueryComponent,
            },
            {
                path: 'bigCustomDetail',
                component: BigCustomDetailComponent
            },
        ]
    }

]
export const BigCusApplyRouting: ModuleWithProviders = RouterModule.forChild(routes);