import { Component, OnInit } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { BigCusHisQryBean } from './bean/bigCusHisQry.bean';
@Component({
    selector: 'bigCustomHistoryQuery',
    templateUrl: './bigCustomHistoryQuery.component.html',
    styleUrls: ['./bigCustomHistoryQuery.component.scss'],
    providers: [Commfunc, BigCusHttpService, ConfirmationService]
})
export class BigCustomHistoryQueryComponent implements OnInit {
    inValue: any = 'take';
    msgs: Message[] = [];
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;//
    pageSize: number = 10;//
    pageNum: number = 1;//
    //
    total: string = '10';
    detailHeader: string = '';
    detailPage: boolean = false;
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    personId: any = [];
    manageId: any;//
    personName: any = [];//
    personPage: boolean = false;
    userId:string ;

    code: any;
    bigCusStatusOptions: any = [];//
    approveStatusOptions: any = [];//
    operationTypeOptions: any = [];//
    first: number = 0;
    dataTable: any = [];

    startDate: Date = null;//
    endDate: Date = null;//
    bigCusHisQryBean: BigCusHisQryBean = new BigCusHisQryBean();
    dataValue: any;//

    //
    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;
    submitNum: string;//
    updateListValue: any = [];
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public httpService: BigCusHttpService,
        public confirmationService: ConfirmationService,
    ) { }

    ngOnInit() {
        this.codeValues();
        this.bigCusStatusOptions = this.code['statusbigCus'];//
        this.approveStatusOptions = this.code['approveStatusbigCus'];//
        this.operationTypeOptions = this.code['operationType'];//
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.userId = s.userId;
        console.log(this.userId);
        this.bigCusHisQryBean.pageNum = 1;
        this.bigCusHisQryBean.pageSize = 10;
        this.query();
    }
    //
    toDetail(data) {
        data['detail_Flag'] = '1';
        this.detailPage = true;
        this.dataValue = data;
    }
    //
    treeCall(param) {
        this.orgTreePage = false;
        if (param.orgId == 'T0000') {
            param.orgId = '0000';
        }
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
    }
    //
    perTreeShow() {
        this.orgTreePage = true;
    }
    //
    perShow() {
        this.personPage = true;
    }
    //
    perInfor(item) {
        console.log(item)
        this.personPage = false;
        this.personName = item[0].tellerName;
        this.personId = item[0].tellerId;
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    toUpdate(data) {
        data['this_Flag'] = '0';
        this.router.navigate(["pages/tzb/custom/bigCustomer/big-cus-apply/bigCustomApply", data])
    }
    //
    toDelete(data) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                this.httpService.deleteBigCustHistory({ bigId: data.bigId }).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '', detail: '!' }];
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                });
            }
        });
    }
    //
    paginate(event) {
        //
        this.bigCusHisQryBean.pageSize = event.rows * 1;
        //
        this.bigCusHisQryBean.pageNum = event.page + 1;
        this.first = event.page * this.bigCusHisQryBean.pageSize;
        this.query();
    }
    //
    queryFirst() {
        this.bigCusHisQryBean.pageNum = 1;
        this.bigCusHisQryBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    query() {
        if (this.treeOrgId && this.treeOrgId != '') {
            this.bigCusHisQryBean.createrOrg = this.treeOrgId;
        }
        if (this.personId && this.personId != '') {
            this.bigCusHisQryBean.creater = this.personId;
        }
        this.bigCusHisQryBean.createdDateStart = this.commfunc.transDateN(this.startDate);
        this.bigCusHisQryBean.createdDateEnd = this.commfunc.transDateN(this.endDate);
        this.httpService.queryBigCustHistory(this.bigCusHisQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data.bigCustHistRelList;
                this.dataTable.forEach((item,index) => {
                    this.httpService.findUser({ custNo: this.dataTable[index].custNo }).subscribe(dataUser => {
                        if (dataUser.returnCode.code == 'success') {
                            if (dataUser.infoList != null) {
                                if (dataUser.infoList.length > 0) {
                                    this.manageId = dataUser.infoList[0].manageId;
                                    this.dataTable[index]['manageId'] = this.manageId;
                                     //console.log( this.dataTable);
                                }
                            }
                        } 
                    })
                });
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    reset() {
        this.bigCusHisQryBean.createrOrg = '';
        this.treeOrgId = '';
        this.treeOrgName = '';
        this.bigCusHisQryBean.creater = '';
        this.personId = '';
        this.personName = '';
        this.bigCusHisQryBean.bigCustNo = '';
        this.bigCusHisQryBean.custNo = '';
        this.bigCusHisQryBean.custName = '';
        this.startDate = null;
        this.endDate = null;
        this.bigCusHisQryBean.operationType = '';
        this.bigCusHisQryBean.statusId = '';
        this.bigCusHisQryBean.approveStatus = '';
    }
    //
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                return
            }
            if (num == 2) {
                if (custNoTen(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                    return
                }
            }
        }
    }
    //
    chectStart() {
        if (this.endDate && this.endDate != null && this.startDate && this.startDate != null) {
            if (this.startDate > this.endDate) {
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                this.startDate = null;
                return;
            }
        }
    }
    //
    chectEnd() {
        if (this.endDate && this.endDate != null && this.startDate && this.startDate != null) {
            if (this.startDate > this.endDate) {
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                this.endDate = null;
                return;
            }
        }
    }
    //
    commit(col) {
        this.updateListValue = col;
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                // setTimeout(() => this.toSubmit(), 100);
                this.submitNum = '1';
                this.submit_1();
            }
        });
    }
    // //
    // toSubmit() {
    //     this.confirmationService.confirm({
    //         message: '?',
    //         accept: () => {
    //             this.submitNum = '0';
    //             this.submit_1();
    //         },
    //         reject: () => {
    //             this.submitNum = '1';
    //             this.submit_1();
    //         }
    //     });
    // }
    submit_1() {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.waitSelUserList = this.commfunc.query('DKHFYSQ', {});
        if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            let chooseThis = this.waitSelUserList.display;
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            let a2 = commonHeader.extParam.specifyNextOpers;
            if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                this.downloadPerson = true;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                return;
            }
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
        }
    }
    //
    submit_2() {
        let param = {
            bigCustNo: this.updateListValue.bigCustNo,
            bigId: this.updateListValue.bigId,
            isDiff: this.submitNum
        }
        this.httpService.approveBigCustApplication(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '!' }];
                this.queryFirst();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.submit_2();
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    //id
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
