import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import * as moment from 'moment';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber, decimalNumTwo } from 'app/@uisftech/common/service/validator';
import { BigCusQryBean } from './bean/bigCusQry.bean';
@Component({
    selector: 'bigCustomApplyQuery',
    templateUrl: './bigCustomApplyQuery.component.html',
    styleUrls: ['./bigCustomApplyQuery.component.scss'],
    providers: [Commfunc, BigCusHttpService, ConfirmationService]
})
export class BigCustomApplyQueryComponent implements OnInit {
    inValue_data: any = 'take';
    first: number = 0;
    code: any;
    msgs: Message[] = [];
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;//
    pageSize: number = 10;//
    pageNum: number = 1;//
    //
    total: string = '10';
    detailHeader: string = '';
    detailPage: boolean = false;
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    personId: any = [];
    personName: any = [];//
    personPage: boolean = false;
    //
    chooseData: any = [];
    checkAll: boolean = false;
    dataTable: any = [];
    startDate: Date = null;//
    endDate: Date = null;//
    today_time = new Date();
    manageId = String;

    bigCusStatusOptions: any = [];
    approveStatusOptions: any = [];//
    operationTypeOptions: any = [];//

    inValue: any;//
    bigCusQryBean: BigCusQryBean = new BigCusQryBean();
    custManagerNosFlag: boolean = false;

    //
    backBigCusNo: string;
    //
    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;

    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    constructor(
        public router: Router,
        public commfunc: Commfunc,
        public httpService: BigCusHttpService,
        public confirmationService: ConfirmationService,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
    }

    ngOnInit() {
        this.codeValues();
        this.bigCusStatusOptions = this.code['statusbigCus'];//
        this.approveStatusOptions = this.code['approveStatusbigCus'];//
        this.operationTypeOptions = this.code['operationType'];//

        this.bigCusQryBean.pageNum = 1;
        this.bigCusQryBean.pageSize = 10;
        this.query();
    }
    toDetail(data) {
        data['detail_Flag'] = '0';
        this.detailPage = true;
        this.inValue = data;
    }
    //
    treeCall(param) {
        this.orgTreePage = false;
        if (param.orgId == 'T0000') {
            param.orgId = '0000';
        }
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
    }
    //
    perTreeShow() {
        this.orgTreePage = true;
    }
    //
    perShow() {
        this.personPage = true;
    }
    //
    perInfor(item) {
        this.personPage = false;
        this.personName = item[0].tellerName;
        this.personId = item[0].tellerId;
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    //
    paginate(event) {
        //
        this.bigCusQryBean.pageSize = event.rows * 1;
        //
        this.bigCusQryBean.pageNum = event.page + 1;
        this.first = event.page * this.bigCusQryBean.pageSize;
        this.query();
    }
    //
    queryFirst() {
        this.bigCusQryBean.pageNum = 1;
        this.bigCusQryBean.pageSize = 10;
        this.first = 0;
        this.query();
    }

    //
    showName() {
        if (this.bigCusQryBean.custNo && this.bigCusQryBean.custNo != '') {
            let param = {
                custNo: this.bigCusQryBean.custNo
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.bigCusQryBean.custName = data.custName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            });
        }
    }
    query() {
        if (this.treeOrgId && this.treeOrgId != '') {
            this.bigCusQryBean.createrOrg = this.treeOrgId;
        }
        if (this.personId && this.personId != '') {
            this.bigCusQryBean.creater = this.personId;
        }
        this.bigCusQryBean.createdDateStart = this.commfunc.transDateN(this.startDate);
        this.bigCusQryBean.createdDateEnd = this.commfunc.transDateN(this.endDate);
        this.httpService.queryBigCustRegister(this.bigCusQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data.bigCustRegisterRelList;
                this.total = data.totalNum;
                this.dataTable.forEach((item,index) => {
                    this.httpService.findUser({ custNo: this.dataTable[index].custNo }).subscribe(dataUser => {
                        if (dataUser.returnCode.code == 'success') {
                            if (dataUser.infoList != null) {
                                if (dataUser.infoList.length > 0) {
                                    this.manageId = dataUser.infoList[0].manageId;
                                    this.dataTable[index]['manageId'] = this.manageId;
                                    //console.log( this.dataTable);
                                }
                            }
                        } 
                    })
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    reset() {

        this.treeOrgId = '';
        this.treeOrgName = '';
        this.bigCusQryBean.creater = '';
        this.personId = '';
        this.personName = '';
        this.bigCusQryBean.bigCustNo = '';
        this.bigCusQryBean.custNo = '';
        this.bigCusQryBean.custName = '';
        this.startDate = null;
        this.endDate = null;
        this.bigCusQryBean.statusId = '';
        this.bigCusQryBean.createdDateStart = '';
        this.bigCusQryBean.createdDateEnd = '';
        this.bigCusQryBean.feeStart = null;
        this.bigCusQryBean.feeEnd = null;
    }
    //
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                return
            }
            if (num == 2) {
                if (custNoTen(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                    return
                }
            }
        }
    }
    //
    checkNumtwo(event) {
        if (event.target.value != '') {
            if (decimalNumTwo(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '', detail: ',!' }];
                event.target.value = '';
                return
            }
        }
    }
    //
    checkfee(data) {
        if (this.bigCusQryBean.feeStart && this.bigCusQryBean.feeStart != null && this.bigCusQryBean.feeEnd && this.bigCusQryBean.feeEnd != null) {
            if (this.bigCusQryBean.feeStart > this.bigCusQryBean.feeEnd) {
                if (data == 1) {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    this.bigCusQryBean.feeStart = null;
                } else if (data == 2) {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    this.bigCusQryBean.feeEnd = null;
                }
            }
        }
    }
    //
    chectStart() {
        if (this.endDate && this.endDate != null && this.startDate && this.startDate != null) {
            if (this.startDate > this.endDate) {
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                this.startDate = null;
                return;
            }
        }
    }
    //
    chectEnd() {
        if (this.endDate && this.endDate != null && this.startDate && this.startDate != null) {
            if (this.startDate > this.endDate) {
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                this.endDate = null;
                return;
            }
        }
    }
    //
    toUpdate(item) {
        let param = {
            bigCustNo: item.bigCustNo,
            userName: this.userName,
        }
        this.httpService.updateBigCustHistory(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                item['bigId'] = data.bigId;
                item['bigCustNo'] = data.bigCustNo;
                item['this_Flag'] = '1';
                this.router.navigate(["pages/tzb/custom/bigCustomer/big-cus-apply/bigCustomApply", item])
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    toGetBack(data) {
        this.backBigCusNo = data.bigCustNo;
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.waitSelUserList = this.commfunc.query('DKHFYCX', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let chooseThis = this.waitSelUserList.display;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a2 = commonHeader.extParam.specifyNextOpers;
                    if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                        this.downloadPerson = true;
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                        return;
                        // this.comeBack();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
                }
            }
        })
    }
    //
    comeBack() {
        let param = {
            bigCustNo: this.backBigCusNo,
            userName: this.userName,
        }
        this.httpService.approveBigCustRevoke(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '!' }];
                this.queryFirst();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }

    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.comeBack();
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    //id
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
