import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
    selector: 'bigCustomDetail',
    templateUrl: './bigCustomDetail.component.html',
    styleUrls: ['./bigCustomDetail.component.scss'],
    providers: [BigCusHttpService]
})
export class BigCustomDetailComponent implements OnInit, OnChanges {
    @Input() dataValue: any;//
    bigCustNo: string;//
    bigId: string;//
    tabMesg: any = tableMessage;//
    show: number = 1;
    first: number = 0;
    data: any = [];
    pageSize: number = 10;//
    pageNum: number = 1;//
    //
    total: string = '10';
    msgs: Message[] = [];
    detailItems = {
        custNo: '',
        custName: '',
        snrjckSum: '',
        snrjdkjckSum: '',
        feeunitPrice: '',
        depositBase: '',
        loanBase: '',
        bigCustNo: '',
    }
    data_cuslist: any = [];//
    pageSizeCusList: number = 10;
    pageNumCusList: number = 1;
    totalCusList: number;

    dataTable: any = [];//
    pageSizeDivide: number = 10;
    pageNumDivide: number = 1;
    totalDivide: number;

    dataApprove: any = [];
    doDeal: any = [];
    code: any;

    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    depositSumTypeOptions: any = [];//
    constructor(
        public httpService: BigCusHttpService,
        public commfunc: Commfunc
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
    }

    ngOnInit() {
        this.codeValues() //
        //,
        this.doDeal = this.code['DoDeal'];
        this.depositSumTypeOptions = this.code['depositSumTypeBigCus'];//

        this.pageSizeCusList = 10;
        this.pageNumCusList = 1;
        console.log(this.dataValue)
        this.bigCustNo = this.dataValue.bigCustNo;
        this.bigId = this.dataValue.bigId;
        this.queryBase();//
        this.queryCusList();//
        this.divideInto();//
    }

    ngOnChanges() {
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    apply(num) {
        this.show = num;
    }
    //
    queryBase() {
        let isthis = ''
        if (this.dataValue.detail_Flag && this.dataValue.detail_Flag == '1') {
            isthis = '1';
        } else if (this.dataValue.detail_Flag && this.dataValue.detail_Flag == '0') {
            isthis = '0';
        }
        let param = {
            bigCustNo: this.bigCustNo,
            isHis: isthis,
            bigId: this.bigId
        }
        this.httpService.queryBigCustBaseInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.detailItems.custNo = data.custNo;
                this.detailItems.custName = data.custName;
                this.detailItems.snrjckSum = data.snrjckSum;
                this.detailItems.snrjdkjckSum = data.snrjdkjckSum;
                this.detailItems.feeunitPrice = data.feeunitPrice;
                this.detailItems.depositBase = data.depositBase;
                this.detailItems.loanBase = data.loanBase;
                this.detailItems.bigCustNo = data.bigCustNo;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    queryCusList() {
        let param = {
            bigCustNo: this.bigCustNo,
            pageNum: this.pageNumCusList,
            pageSize: this.pageSizeCusList,
            userName: this.userName
        }
        this.httpService.queryBigCustInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.data_cuslist = data.bigCustInfoNewList;
                this.totalCusList = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    paginateCusList(event) {
        this.pageSizeCusList = event.rows * 1;
        this.pageNumCusList = event.page + 1;
        this.queryCusList();
    }
    //
    toDelete(data) {
        let temp = {
            bigCustId: data.bigId,
        }
        this.httpService.deleteBigCustInfo(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '!' }];
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    divideInto() {
        let isthis = ''
        if (this.dataValue.detail_Flag && this.dataValue.detail_Flag == '1') {
            isthis = '1';//  
        } else if (this.dataValue.detail_Flag && this.dataValue.detail_Flag == '0') {
            isthis = '0';//  
        }
        let param = {
            bigCustNo: this.dataValue['bigCustNo'],
            pageNum: this.pageNumDivide,
            pageSize: this.pageSizeDivide,
            isHis: isthis
        }
        this.httpService.queryBigCustManager(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.bigCustManNewList && data.bigCustManNewList.length > 0) {
                    for (let item of data.bigCustManNewList) {
                        item.divideRate = JSON.parse(JSON.stringify(item.divideRate * 100));
                    }
                    this.dataTable = data.bigCustManNewList;
                    this.totalDivide = data.totalNum;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    paginateDivide(event) {
        this.pageSizeDivide = event.rows * 1;
        this.pageNumDivide = event.page + 1;
        this.divideInto();
    }
    // ()
    bpmQueryTaskList() {
        let param = {
            processId: '115000',
            isAllNode: true,
        }
        this.httpService.bpmQueryTaskList(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.dataApprove = data.pageData.result;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

}
